body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "IBM Plex Mono", monospace;
  font-size: 20px;
  color: #222;
}
main {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-template-rows: 1fr;
}
#photo {
  height: 100%;
  width: 400px;
}
#photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
#content {
  box-sizing: border-box;
  padding: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
h1 {
  font-size: 2.4rem;
  line-height: 54px;
  margin-bottom: 1rem;
}
h2 {
  margin-top: 2rem;
  font-size: 1.2rem;
}
h3 {
  font-size: 1rem;
}
p {
  font-size: 1rem;
  line-height: 36px;
  margin-bottom: 1rem;
}
a {
  color: #06f;
}
#experience {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-column-gap: 40px;
}
#previously .experience-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 32px;
}
.employer {
  display: grid;
  grid-template-rows: 44px 60px;
}
.employer p {
  font-size: 0.8rem;
}
#funda {
  height: 40px;
}
#suitsupply {
  margin-top: 8px;
  height: 20px;
}

#bolcom {
  margin-top: -9px;
  height: 38px;
}
